import cuCustom from '@/colorui/components/cu-custom.vue'
export default {
	components: {
		cuCustom
	},
	data() {
		return {
			host: "http://47.104.29.236:9999/api/v1",
			// host: "http://127.0.0.1:9999/api/v1",
			win_id: 0,
			list: [],
			classify: [],
			menu: [],
			tabCur: 0,
			mainCur: 0,
			verticalNavTop: 0,
			load: true,
			user_id: 0,
			stusersid: 0,
			// 购物车中的数量
			care_num: 0,
			// 窗口名称
			win_name: ""
		};
	},
	onLoad(option) {
		this.win_id = option.id;
		console.log(this.win_id);
		// 获取到用户id
		let id = uni.getStorageSync('user_id')
		this.user_id = id

	},
	onReady() {
		uni.hideLoading()
	},

	methods: {
		// 获取到商铺用户id
		getStusersId() {
			// 获取到窗口id
			// let id = uni.getStorageSync('winid')
			// this.win_id = id.win_id
			uni.request({
				url: `${this.host}/stusers_id`,
				data: {
					win_id: this.win_id
				},
				success: (data) => {
					this.stusersid = data.data.data[0].stuser_id
				}
			})
		},
		// 添加到购物车
		addcare(e) {
			// console.log(this.user_id)
			// console.log(this.stusersid);
			// console.log(e.target.id);
			uni.request({
				url: `${this.host}/add_care`,
				method: 'post',
				data: {
					user_id: this.user_id,
					menu_id: e.target.id,
					stusersid: this.stusersid
				},
				success: () => {
					this.getcare();
				}
			})
		},
		TabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
			this.mainCur = e.currentTarget.dataset.id;
			this.verticalNavTop = (e.currentTarget.dataset.id - 1) * 50
		},

		VerticalMain(e) {



			let that = this;
			let tabHeight = 0;
			if (this.load) {
				for (let i = 0; i < this.list.length; i++) {
					let view = uni.createSelectorQuery().select("#main-" + this.list[i].id);
					view.fields({
						size: true
					}, data => {
						this.list[i].top = tabHeight;
						tabHeight = tabHeight + data.height;
						this.list[i].bottom = tabHeight;
					}).exec();
				}
				this.load = false
			}
			let scrollTop = e.detail.scrollTop + 10;
			for (let i = 0; i < this.list.length; i++) {
				if (scrollTop > this.list[i].top && scrollTop < this.list[i].bottom) {
					this.verticalNavTop = (this.list[i].id - 1) * 50
					this.tabCur = this.list[i].id
					console.log(scrollTop)
					return false
				}
			}
		},
		// 获取到分类
		getClassify() {
			uni.request({
				url: `${this.host}/classify_list_st/${this.win_id}`,
				success: (data) => {
					this.menu = data.data.data.menu;
					this.classify = data.data.data.classify;
				}
			})
		},
		// 点击产看菜品
		lookmenu(id) {
			uni.setStorage({
				key: 'winid',
				data: {
					win_id: this.win_id
				},
			});
			uni.reLaunch({
				url: `/pages/meituan/menu?id=${id}`
			});
		},
		// 获取到购物车中的数据数量
		getcare() {
			console.log(this.user_id);
			uni.request({
				url: `${this.host}/care_st`,
				data: {
					user_id: this.user_id
				},
				success: (res) => {
					this.care_num = res.data.data[0].care_num
				}
			})
		},
		// 点击购物车进行跳转
		clickcare() {
			let CurrentPages = getCurrentPages()
			uni.reLaunch({
				url: `/pages/meituan/myCart?win_id=${this.win_id}&path=${CurrentPages[0].route}`
			});
		},
		// 获取到对应的标题
		getwin() {
			console.log(this.win_id);
			uni.request({
				url: `${this.host}/stusers_id`,
				data: {
					win_id: this.win_id
				},
				success: (res) => {
					console.log(res);
					this.win_name = res.data.data[0].win_name
				}
			})
		}
	},
	onShow() {
		this.getStusersId();
		this.getClassify();
		this.getcare();
		// this.getwin();
	}
}
